// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
//  
import Vue from 'vue'
import axios from 'axios' //  axios 
import VueAxios from 'vue-axios' //  vue-axios 
import Loading from 'vue-loading-overlay'; //  vue-loading-overlay 
import VeeValidate from 'vee-validate'; //  vee-validate 
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap'

//  
import App from './App'
import router from './router'
import currency from './filters/currency'
import data from './filters/data'

//  
axios.defaults.withCredentials = true;
Vue.config.productionTip = false
Vue.use(VueAxios, axios)
Vue.use(VeeValidate);
Vue.filter('currency', currency)
Vue.filter('data', data)
Vue.component('loading', Loading)

/* eslint-disable no-new */
new Vue({
    el: '#app',
    components: { App },
    template: '<App/>',
    router
})

// 
router.beforeEach((to, from, next) => {
    if (to.meta.requiresAuth) {
        const api = `${process.env.APIPATH}/api/user/check`;
        axios.post(api).then((response) => {
            console.log(response.data);
            //  success 
            (response.data.success) ? next(): next({
                path: '/login',
                alert: function() {
                    alert('please resign in again !')
                }()
            })
        })
    } else {
        next() // 
    }
})


