//  
import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/components/pages/Login'
import Dashboard from '@/components/Dashboard'
import Products from '@/components/pages/Products'
import Order from '@/components/pages/Order'
import Coupon from '@/components/pages/Coupon'
import OrderMain from '@/components/pages/OrderMain'
import Checkout from '@/components/pages/Checkout'

// 
Vue.use(VueRouter)
var ms = 1
    // router 
export default new VueRouter({
    routes: [{
        // /login
        path: '*',
        redirect: '/login',
    }, {
        path: '/login',
        name: 'Login',
        component: Login
    }, {
        path: '/control',
        name: 'Dashboard',
        component: Dashboard,
        children: [{
            path: 'products',
            name: 'Products',
            component: Products,
            meta: { requiresAuth: true } // 
        }, {
            path: 'coupon',
            name: 'Coupon',
            component: Coupon,
            meta: { requiresAuth: true } // 
        }, {
            path: 'order',
            name: 'Order',
            component: Order,
            meta: { requiresAuth: true } // 
        }]
    }, { // 
        path: '/',
        name: 'Dashboard',
        component: Dashboard,
        children: [{
                path: 'ordermain',
                name: 'OrderMain',
                component: OrderMain
            },
            {
                path: 'checkout/:orderId',
                name: 'Checkout',
                component: Checkout
            }
        ]
    }]
})


